<template>
  <div class="login">
    <div id="poster">
      <h1 class="logo">后台饮品配置管理</h1>
      <el-form class="login-container" label-position="left" label-width="0px">
        <h3 class="login_title">系统登录</h3>
        <el-form-item>
          <el-input v-model="loginForm.account" placeholder="请输入帐号"></el-input>
        </el-form-item>

        <el-form-item>
          <el-input placeholder="请输入密码" v-model="loginForm.password" @keyup.enter.native="login" show-password></el-input>
        </el-form-item>

        <el-form-item style="width: 100%">
          <el-button type="primary" style="width: 100%;background: #505458;border: none" @click.stop="login">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import menuList from '@/utils/menu'
export default {
  name: 'Login',
  data() {
    return {
      loginForm: {
        account: '',
        password: ''
      },
      menuList: []
    }
  },
  methods: {
    login() {
      if (this.loginForm.account === '') return this.$message.warning('请输入帐号')
      if (this.loginForm.password === '') return this.$message.warning('请输入密码')

      const params = { account: this.loginForm.account, password: this.$md5(this.loginForm.password) }
      this.httpPost('/basic/employee/login', params).then(ret => {
        this.$sessionData('set', 'USER', ret)
        // 权限菜单
        let menuList = this.$deepClone(this.menuList)
        let list = ret.resourceList
        // console.log(menuList)
        // console.log(list)
        // 一级
        menuList.filter(m => list.filter(l => l.href === m.path).length > 0).forEach(m => (m.auth = true))
        // 二级
        menuList
          .filter(m => m.childNode)
          .flatMap(m => m.childNode)
          .filter(m => list.filter(l => l.href === m.path).length > 0)
          .forEach(m => (m.auth = true))
        this.$sessionData('set', 'menuList', menuList)
        // 权限菜单

        if (!location.hash) {
          if (location.search && location.search.slice(0, 1) === '?') {
            this.$router.replace('/' + location.search.split('?')[1])
          } else if (location.href.split('/login')[1].indexOf('?') === 0) {
            this.$router.replace('/' + location.href.split('/login?')[1])
          } else {
            this.$router.push('/home')
          }
        } else {
          if (location.hash === '#/login') {
            this.$router.push('/home')
          } else {
            this.$router.replace('/' + location.hash.split('/login?')[1])
          }
        }
      })
    }
  },
  mounted() {
    this.menuList = this.$deepClone(menuList)
    // console.log(window.innerHeight)
    // document.getElementById('poster').style.height = window.innerHeight + 'px'
  }
}
</script>

<style lang="scss" scoped>
.login {
  background: url('../assets/images/timg.jpg') no-repeat;
  background-size: cover;
  height: 100vh;
  width: 100%;
}
#poster {
  width: 800px;
  height: 500px;
  text-align: center;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

.logo {
  margin: 0;
  font-size: 52px;
  color: #fff;
  margin-bottom: 50px;
}
.login-container {
  width: 420px;
  height: 300px;
  border-radius: 15px;
  background-clip: padding-box;
  padding: 35px 35px 15px 35px;
  background-color: hsla(0, 0%, 100%, 0.9);
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  margin: auto;
}

.login_title {
  margin: 0px auto 20px;
  text-align: center;
  color: #505458;
  font-size: 20px;
}

.el-form-item {
  margin-bottom: 20px;
}

/deep/input {
  height: 40px;
}

.el-button {
  margin-top: 20px;
  height: 40px;
}
/deep/.footer {
  height: auto !important;
  padding-top: 20px;
  text-align: center;
}
</style>
